.table{
    height: 400px;
    margin: 30px 0;
    overflow: scroll;
    overflow-x: hidden;
}
.table h4{
    margin-bottom: 20px;
}

 table {
     
    display: flex;
    justify-content: space-between;
}

 table td{
    padding: 5px;
}

.table_row:nth-child(odd){
    background: #f0f0f3;;
}

