@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

body{
  background: #f4f6fa;
}

.app{
 
  padding: 20px;
  margin-bottom: 30px;
}

.app__content{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.app__left{
  -webkit-flex: 0.8 1;
          flex: 0.8 1;
}

/* .app__right{
  width: 250px;
} */



.app__header{
  -webkit-align-items: center;
          align-items: center;
  background: rgb(255, 255, 255);
  box-shadow: 2px 2px lightgray ;
  color: #04a1f0;
  height: 70px;
  opacity: 0.9;
  padding: 5px;
  display: -webkit-flex;
  display: flex;
  margin-bottom: 30px;
 
}


.app__header__image {
  width: 60px;
  height: 60px;
  margin: 0 20px;
}

.app__header__image img{
  width: 100%;
  height: 100%;
}
.app__dropdown__select{
  width: 150px;
  height: 40px;
 
}

.MuiSelect-root.MuiSelect-select.MuiSelect-selectMenu .MuiSelect-outlined .MuiInputBase-input .MuiOutlinedInput-input:focus{
  background: white;
  outline: none;
}

.MuiSvgIcon-root.MuiSelect-icon.MuiSelect-iconOutlined{
  color: white;
  background: linear-gradient(#55bef3, #72f8ed);
}





.MuiPaper-root .MuiCard-root .app__right .MuiPaper-elevation1 .MuiPaper-rounded{
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}





.statsBox{
  background: #f4f6fa;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 20px;
  padding: 30px;
  
  height:-webkit-fit-content;
  
  height:-moz-fit-content;
  
  height:fit-content;
  
}





@media (max-width: 990px) {
  .app__content, .countryInfo__container, .statsBox{
    -webkit-flex-direction: column;
            flex-direction: column;
  }

 .statsBox{
   -webkit-flex-direction: row;
           flex-direction: row;
   -webkit-flex-wrap: wrap;
           flex-wrap: wrap;
 }

 .MuiPaper-root.MuiCard-root.MuiPaper-elevation1.MuiPaper-rounded{
   width: -webkit-fit-content;
   width: -moz-fit-content;
   width: fit-content;
   margin: 10px;
 }

 .MuiCardContent-root.cases, .MuiCardContent-root.deaths,
 .MuiCardContent-root.recovered, .MuiCardContent-root.vaccinated{
  width: 150px;
  height: 127px;
  text-align: center;
}


}

@media (max-width: 517px) {


  .MuiCardContent-root.countryInfo__cardContent{
    width: 350px;
    padding-left: 40px;
  }

  .MuiPaper-root.MuiCard-root.MuiPaper-elevation1.MuiPaper-rounded{
    margin: 0 auto;
    margin-bottom: 30px;
  }
  .MuiCardContent-root.cases, .MuiCardContent-root.deaths,
 .MuiCardContent-root.recovered, .MuiCardContent-root.vaccinated{
   width: 250px;
 }
}

.statsBox:nth-child(1) {
    background: red;
   }
   
   .statsBox .MuiPaper-root{
     margin: 0 10px;
     padding: 0 10px;
   }
   
   
   
   
   /* .MuiCardContent-root:hover{
    background: beige;
   } */
   
   .MuiCardContent-root.cases, .MuiCardContent-root.deaths{
     border-bottom: 10px solid red;
     cursor: pointer;
   }
   
   .MuiCardContent-root.recovered{
     border-bottom: 10px solid rgb(101, 255, 87);
     cursor: pointer;
   }
   
   .MuiCardContent-root.vaccinated{
     border-bottom: 10px solid rgb(3, 18, 150);
   }
   

   /*HOVER*/

   .MuiCardContent-root.cases:hover, .MuiCardContent-root.deaths:hover{
    color:red;
    cursor: pointer;
  }

   .MuiCardContent-root.recovered:hover{
    color: rgb(101, 255, 87);
  }

   .MuiCardContent-root.vaccinated:hover{
    color:rgb(3, 18, 150);  }
   
   .timestamp{
     margin: 3px;
   }
   
   @media (max-width: 400px){
    .MuiCardContent-root.cases, .MuiCardContent-root.deaths, .MuiCardContent-root.recovered, .MuiCardContent-root.vaccinated {
      width: 150px;
      margin: 20px 0;
  }
  .MuiCardContent-root.cases, .MuiCardContent-root.deaths, .MuiCardContent-root.recovered, .MuiCardContent-root.vaccinated {
   margin: 20px 0;
}
   }

.countryInfo__container{
    display: -webkit-flex;
    display: flex;
    margin: 20px 0 ;
    padding: 15px;
   
  }
  
  .countryInfo{
    margin: 10px 0;
    color: #f4f6fa;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  
    
    
  }


  
.MuiCardContent-root.countryInfo__cardContent{
    background: linear-gradient(#55bef3, #72f8ed);
    -webkit-align-items: center;
            align-items: center;
  }
  
  .MuiCardContent-root.countryInfo__cardContent h2{
    color: azure;
  }
  
  .countryInfo img{
    width: 250px;
    border-radius: 5px;
  }
  
  .countryInfo h2{
    margin: 10px 0;
  }
  

  @media (max-width: 400px) {


    .MuiCardContent-root.countryInfo__cardContent{
     width: 300px;
    }

    .MuiCardContent-root.countryInfo__cardContent img{
      width: 220px;
     }
     
    .MuiCardContent-root.countryInfo__cardContent h2{
      font-size: 30px;
     }
    
  }
.lineGraph{
    margin: 20px 0;
    padding: 2px;
    height: 250px;
  
  }
  .lineGraph h4{
    margin-bottom: 10px;
  }

  .MuiCardContent-root .lineGraph_Card{
    -webkit-flex: 1 1;
            flex: 1 1;
  }
.table{
    height: 400px;
    margin: 30px 0;
    overflow: scroll;
    overflow-x: hidden;
}
.table h4{
    margin-bottom: 20px;
}

 table {
     
    display: -webkit-flex;
     
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

 table td{
    padding: 5px;
}

.table_row:nth-child(odd){
    background: #f0f0f3;;
}


.world__summary{
    margin: 20px 0;
    width: 100%;
    
}

.world__summary h3{
    margin: 20px;
    font-size: 22px;
}


.world__summary__content{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    font-size: 20px;

}

.world__summary__content h4{
  margin: 15px 0;
}

.world__summary__content .MuiCardContent-root{
   width: 200px;
   text-align: center;
}


@media (max-width: 990px){
  
    .world__summary__content{
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
    }
}


@media (max-width: 517px) {
    .world__summary__content{
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-flex-wrap: nowrap;
                flex-wrap: nowrap;
    }
    .world__summary__content .MuiCardContent-root{
        width: 250px;
     }
     
}

@media (max-width: 400px) {
    .world__summary__content{
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-flex-wrap: nowrap;
                flex-wrap: nowrap;
    }
    .world__summary__content .MuiCardContent-root{
        width: 150px;
     }
     
}


.map{
    height: 400px;
    background: white;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 16px;
    box-shadow: 0 0 8px -4px rgba(0,0,0,0.5);
}

.map .leaflet-container {
    height: 100%;
}

.leaflet-popup-content-wrapper{
    background:linear-gradient(#55bef3, #72f8ed);;
}

.leaflet-popup-content-wrapper h3{
    color: white;
}

.leaflet-popup-content-wrapper h4{
    color: rgba(0,0,0,0.54);
}
